export default {
    data(){
        return {
            lancamentos: [],
            SwiperOptions: {
                slidesPerView: 3,
                spaceBetween: 200,
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true
                },
                breakpoints: {
                    1024: {
                        slidesPerView: 3
                    },
                    768: {
                        slidesPerView: 2
                    },
                    300: {
                        slidesPerView: 1
                    }
                }
            }
        }
    },
    methods: {
        async getLancamentos(){
            this.lancamentos = await this.$api.get('lancamentos')
            this.lancamentos = this.lancamentos.data
        },
        getImage(img){
            return `https://kvsh-api.musiclounge.com.br${img}`
        }
    },
    mounted(){
        this.getLancamentos()
    }
}
